import React, { Component } from 'react';
import '../styles/project.css';
import { withTranslation } from 'react-i18next';

class Project extends Component {
    render() {
        let item = this.props.item;

        let classes = "2xl:ml-48";

        if (this.props.index%2) {
            classes="2xl:mr-48"
        }

        let icons = item.icons.map((item, index) =>
            <img className="h-8" src={process.env.PUBLIC_URL + "/images/" + item + ".png"} alt={"Logo " + item} key={index} /> 
        );

        const { t } = this.props;

        return (
        <div className={"project bg-white dark:bg-gray-800 items-center rounded-xl shadow-md p-6 space-x-2 mb-4 mr-2 gap-2 lg:w-7/12 xl:w-full 2xl:w-10/12 xl:grid-cols-2 xl:grid " + classes}>
            {this.props.index%2 ?
            <>
                <div className="p-4">
                    <div className="text-xl font-medium text-black dark:text-white">{t(item.title)}</div>
                    <p className="text-gray-500 dark:text-gray-100 xl:w-80 2xl:w-96">{t(item.description)}</p>
                    <div className="flex mt-2 items-center justify-between">
                        <div className="flex space-x-2">     
                            {icons}                  
                        </div>
                        <div className="space-x-2 pl-6">
                            {item.repository ? <a href={item.repository} className="dark:bg-gray-500 hover:bg-gray-700 bg-gray-900 text-white font-bold py-2 px-4 rounded">Repository</a> : null}
                            {item.website ? <a href={item.website} className="dark:bg-gray-500 hover:bg-gray-700 bg-gray-900 text-white font-bold py-2 px-4 rounded">Website</a> : null}
                        </div>
                    </div>
                </div>
                <div className="flex">
                    <img className="xl:max-w-xs 2xl:max-w-sm max-h-72 m-auto" src={process.env.PUBLIC_URL + "/images/" + item.illustration + ".png"} alt="Illustration of the project" />
                </div>
            </> :
            <>
                <div className="flex">
                    <img className="xl:max-w-xs 2xl:max-w-sm max-h-72 m-auto" src={process.env.PUBLIC_URL + "/images/" + item.illustration + ".png"} alt="Illustration of the project" />
                </div>
                <div>
                    <div className="text-xl font-medium text-black dark:text-white">{t(item.title)}</div>
                    <p className="text-gray-500 dark:text-gray-100 xl:w-80 2xl:w-96">{t(item.description)}</p>
                    <div className="flex mt-2 items-center justify-between">
                        <div className="space-x-2">
                            {item.repository ? <a href={item.repository} className="dark:bg-gray-500 hover:bg-gray-700 bg-gray-900 text-white font-bold py-2 px-4 rounded">Repository</a> : null}
                            {item.website ? <a href={item.website} className="dark:bg-gray-500 hover:bg-gray-700 bg-gray-900 text-white font-bold py-2 px-4 rounded">Website</a> : null}
                        </div>
                        <div className="flex space-x-2 pr-6">     
                            {icons}                  
                        </div>
                    </div>
                </div>
            </>
            }
        </div>
        );
    }
}

export default withTranslation()(Project);
