import React, { Component, Suspense } from 'react';
import Background from './components/Background';
import About from './components/About';
import ProjectsList from './components/ProjectsList';
import { withTranslation } from 'react-i18next';

class App extends Component {

  constructor(props) {
      super(props);
      this.state = { 
          widthScreen: 0, 
          heightScreen: 0,
      };
  }

  componentDidMount() {
      this.setState({ widthScreen: window.innerWidth, heightScreen: window.innerHeight });
      if(!localStorage.theme) localStorage.theme = 'light';
      if (localStorage.theme === 'dark' || (!('theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
        document.documentElement.classList.add('dark')
      } else {
        document.documentElement.classList.remove('dark')
      }
      
      if (localStorage.lng) {
        const { i18n } = this.props;
        i18n.changeLanguage(localStorage.lng);
      }
  }

  _onMouseMove(e){
      let width = Math.round((Number(e.clientX)/this.state.widthScreen)*360);
      let height = Math.round((Number(e.clientY)/this.state.heightScreen)*100)+10;
      document.querySelectorAll('.itemBg').forEach(e => e.style.width = `${height}%`);
      document.querySelectorAll('.itemBg').forEach(e => e.style.transform = `rotate(${width}deg)`);
  }

  render() {
    return (
      <Suspense fallback="loading">
        <div onMouseMove={this._onMouseMove.bind(this)}>
          <Background />
          <About />
          <ProjectsList />
        </div>
      </Suspense>
    );
  }
}

export default withTranslation()(App);
