import React, { Component } from 'react';
import '../styles/about.css';
import Switch from '@material-ui/core/Switch';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import { withTranslation } from 'react-i18next';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';

class About extends Component {

    constructor(props) {
        super(props);
        this.state = {
            display: true,
            darkMode: false,
        };

        this._toggleDarkMode = this._toggleDarkMode.bind(this);
        this._toggleDisplay = this._toggleDisplay.bind(this);
    }

    componentDidMount() {
        if (localStorage.theme === 'dark') this.setState({
            darkMode: true
        });
    }

    _toggleDarkMode() {
        if (localStorage.theme === 'dark') {
            localStorage.theme = 'light';
            document.documentElement.classList.remove('dark');
        } else {
            localStorage.theme = 'dark';
            document.documentElement.classList.add('dark');
        }
        this.setState({ darkMode: !this.state.darkMode });
    }

    _toggleDisplay() {
        this.setState({
            display: !this.state.display
        });
    }

    render() {
        const { t, i18n } = this.props;

        const changeLanguage = lng => {          
            i18n.changeLanguage(lng);
            localStorage.lng = lng;
        };

        return (
            <>
            {this.state.display ? 
            <div className="about bg-white dark:bg-gray-800 dark:text-white items-center rounded-xl shadow-md p-6 space-y-4 md:w-11/12 m-6 lg:max-w-sm bottom-0 lg:bottom-auto">
                <div className="bg-white dark:bg-gray-800 dark:text-white cursor-pointer lg:hidden z-50" onClick={this._toggleDisplay}>
                    <CloseIcon />                
                </div>
                <div className="flex space-x-4 items-center">
                    <div className="flex-shrink-0">
                        <img className="h-28 w-28" src={process.env.PUBLIC_URL + "/images/avatar.jpg"} alt="Avatar" />
                    </div>
                    <div>
                        <div className="text-xl font-medium text-black dark:text-white">Balzac baudemont</div>
                        <p className="text-gray-500 dark:text-white">{t('job')}</p>
                        <p className="text-gray-500 dark:text-white">{t('degree')}</p>
                    </div>
                </div>
                <div>
                    <p className="text-lg text-black dark:text-white">{t('technologies')} :</p>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/react.png"} alt="Logo reactjS" />
                        <p className="text-black dark:text-white italic">React.js / React-native</p>
                    </div>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/vue.png"} alt="Logo vueJS" />
                        <p className="text-black dark:text-white italic">Vue.js</p>
                    </div>
                </div>
                <div>
                    <p className="text-lg text-black dark:text-white">{t('libraries')} :</p>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/tailwind.png"} alt="Logo tailwind" />
                        <p className="text-black dark:text-white italic">Tailwind</p>
                    </div>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/materialUI.png"} alt="Logo material-UI" />
                        <p className="text-black dark:text-white italic">Material-UI</p>
                    </div>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/bootstrap.png"} alt="Logo bootstrap" />
                        <p className="text-black dark:text-white italic">Bootstrap</p>
                    </div>
                </div>
                <div>
                    <p className="text-lg text-black dark:text-white">{t('mockupDesign')} :</p>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/figma.png"} alt="Logo figma" />
                        <p className="text-black dark:text-white italic">Figma</p>
                    </div>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/photoshop.png"} alt="Logo photoshop" />
                        <p className="text-black dark:text-white italic">Adobe Photoshop</p>
                    </div>
                    <div className="flex space-x-2 items-center ml-4 m-2">
                        <img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/illustrator.png"} alt="Logo illustrator" />
                        <p className="text-black dark:text-white italic">Adobe Illustrator</p>
                    </div>
                </div>
                <div className="flex flex-col justify-center">
                    <div className="flex flex-row justify-center space-x-4 mb-2">
                        <a href="https://github.com/balzacbdmt"><img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/github.png"} alt="Logo github" /></a>
                        <a href="https://www.linkedin.com/in/balzac-baudemont-796a011b1/"><img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/linkedIn.png"} alt="Logo linkedIn" /></a>
                        <a href={process.env.PUBLIC_URL + "/content/CV_Balzac_Baudemont.pdf"}><img className="w-10 p-2" src={process.env.PUBLIC_URL + "/images/cv.png"} alt="Logo CV" /></a>
                    </div>
                    <a className="text-black dark:text-white italic text-center" href="mailto:balzac.baudemont@gmail.com">balzac.baudemont@gmail.com</a>
                </div>
                <div className="flex justify-between items-center">
                    <div className="flex space-x-2">
                        <img className="h-6 w-10" src={process.env.PUBLIC_URL + "/images/Flag_France.png"} alt="French flag" onClick={() => changeLanguage('fr')} />
                        <img className="h-6 w-10" src={process.env.PUBLIC_URL + "/images/Flag_United_Kingdom.png"} alt="United kingdom flag" onClick={() => changeLanguage('en')} />
                    </div>
                    <div>
                        <Brightness4Icon />
                        <Switch
                            checked={this.state.darkMode}
                            onChange={this._toggleDarkMode}
                            name="Dark mode"
                            color="default"
                        />
                    </div>
                </div> 
            </div>
            : 
            <div className="absolute top-2 left-2 m-2 p-2 bg-white dark:bg-gray-800 dark:text-white rounded-xl shadow-md cursor-pointer lg:hidden z-50" onClick={this._toggleDisplay}>
                <MenuIcon className="text-black dark:text-white" />                
            </div>}
            </>
        );
    }
}

export default withTranslation()(About);
